import React from "react";
import ToDoItem from "./ToDoItem";
import Media from 'react-media'

export default function ToDOs(props) {

  
  return (
    <div className="container content-center">
      <h3 className="my-3">ToDOs List</h3>
      {/* {props.todos} */}

      {props.todos.length === 0 ? (
        <div className="alert text-center alert-primary" role="alert">
          All Finished!!
          <br /> No work to do{" "}
        </div>
      ) : (

        // <Media queries={{
        //   small: "(max-width: 999px)",
        //   large: "(min-width: 1000px)"
        // }}>
        //   {matches => (
            // <Fragment>
        //       {matches.small && <p>I am small!</p>}
        //       {matches.large && <p>I am large!</p>}
        //     </Fragment>
        //   )}
        // </Media>
        <Media queries={{
          small: "(max-width: 800px)",
          medium: "(min-width: 800px) and (max-width: 1200px)",
          large: "(min-width: 1200px)"
        }}>
        {matches=>(
          <React.Fragment>
        {matches.large && <div className="grid-container" style={{display: "grid",gridTemplateColumns: "repeat(3, 1fr)",padding: "10px"}}>
          {props.todos.map((item) => {
            return (
              <>
                <div class="grid-item" style={{padding: "20px", textAlign : "center"}}>
                  <ToDoItem
                    todos={item}
                    key={item.sno}
                    onDelete={props.onDelete}
                  />
                </div>
              </>
            );
          })}
        </div>}
        
        {matches.medium && <div className="grid-container" style={{display: "grid",gridTemplateColumns: "repeat(2, 1fr)",padding: "10px"}}>
          {props.todos.map((item) => {
            return (
              <>
                <div class="grid-item" style={{padding: "20px", textAlign : "center"}}>
                  <ToDoItem
                    todos={item}
                    key={item.sno}
                    onDelete={props.onDelete}
                  />
                </div>
              </>
            );
          })}
        </div>}
        {matches.small && <div className="grid-container" style={{alignContent:"center",display: "grid", gridTemplateColumns: "repeat(1, 1fr)",padding: "10px"}}>
          {props.todos.map((item) => {
            return (
              <>
                <div class="grid-item" style={{padding: "20px", textAlign : "center"}}>
                  <ToDoItem
                    todos={item}
                    key={item.sno}
                    onDelete={props.onDelete}
                  />
                </div>
                
              </>
            );
          })}
        </div>}
        </React.Fragment>
        )}
        </Media>
        
      )}
    </div>
  );
}
